.tab-component__buttons {
  display: flex;
  justify-content: center;
  gap: var(--mb-1);
  margin-bottom: var(--mb-3);
}

.tab-component__btn {
  cursor: pointer;
  border: 1px solid var(--dark-color);
  border-radius: var(--border-radius);
  padding: var(--mb-1) var(--mb-2);
  font-size: clamp(1rem, 0.964rem + 0.18vw, 1.125rem);
  font-weight: var(--semibold-font);
  line-height: var(--line-height);
  background-color: var(--light-color);
  transition: background-color 0.2s ease-in-out;
}

.tab-component__btn.active {
  color: var(--white-color);
  background: var(--red-color);
}

.tab-component__contents .tab-component__content {
  display: none;
}

.tab-component__contents .tab-component__content.active {
  display: block;
}

.tab {
  &__row {
    grid-template-columns: repeat(2, 1fr);
    gap: var(--mb-3);
    padding: var(--mb-3) 0;
  }

  &__title {
    margin-bottom: var(--mb-1);
    font-size: clamp(1.25rem, 1.143rem + 0.54vw, 1.625rem);
  }

  &__list {
    margin-bottom: var(--mb-3);
    padding-left: var(--mb-3);
    list-style: square;

    &-item {
      &:not(:last-child) {
        margin-bottom: var(--mb-1);
      }
    }
  }

  &__text {
    margin-bottom: var(--mb-1);

    strong {
      font-size: clamp(1.25rem, 1.179rem + 0.36vw, 1.5rem);
      font-weight: var(--semibold-font);
    }

    span {
      font-size: clamp(1.25rem, 1.179rem + 0.36vw, 1.5rem);
      font-weight: var(--semibold-font);
      color: var(--red-color);
    }

    &:last-of-type {
      margin-bottom: var(--mb-3);
    }
  }
}

@media screen and (max-width:767px) {
  .tab {
    &__row {
      grid-template-columns: 1fr;
    }
  }
}

@media screen and (max-width:767px) {
  .tab-component__buttons {
    flex-direction: column;
  }
}
