.rate {
  &__container {
    grid-template-columns: repeat(2, 1fr);
    gap: var(--mb-3);
  }

  &__list {
    grid-template-columns: repeat(2, 1fr);
  }

  &__link {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    padding: var(--mb-1);
  }

  &__item {
    position: relative;
    overflow: hidden;
    border-right: 1px solid #ddd;

    &-title {
      margin-bottom: var(--mb-0-5);
      font-size: clamp(1.125rem, 0.982rem + 0.71vw, 1.625rem);
      color: var(--dark-blue-color);
      line-height: var(--line-height);
      transition: color .2s ease-in-out;
    }

    &-text {
      font-size: clamp(0.875rem, 0.839rem + 0.18vw, 1rem);
      color: var(--dark-grey-color);
      line-height: var(--line-height);
      transition: opacity .2s ease-in-out;
    }

    &:nth-child(-n+2) {
      border-bottom: 1px solid #ddd;
    }

    &:nth-child(2n) {
      border-right: none;
    }
  }
}

.rate__item {

  &::before,
  &::after {
    content: "";
    position: absolute;
    background-color: var(--dark-blue-color);
    transition: all 0.3s linear;
    z-index: 1;
  }

  &::before {
    height: 2px;
    width: 0%;
    top: 0;
    left: 0;
    transition-delay: 0s;
  }

  &::after {
    width: 2px;
    height: 0%;
    top: 0;
    right: 0;
    transition-delay: 0.3s;
  }

  .rate__link::before,
  .rate__link::after {
    content: "";
    position: absolute;
    background-color: var(--dark-blue-color);
    transition: all 0.3s linear;
    z-index: 1;
  }

  .rate__link::before {
    height: 2px;
    width: 0%;
    bottom: 0;
    right: 0;
    transition-delay: 0.6s;
  }

  .rate__link::after {
    width: 2px;
    height: 0%;
    bottom: 0;
    left: 0;
    transition-delay: 0.9s;
  }

  &:hover::before {
    width: 100%;
    transition-delay: 0s;
  }

  &:hover::after {
    height: 100%;
    transition-delay: 0.3s;
  }

  &:hover .rate__link::before {
    width: 100%;
    transition-delay: 0.6s;
  }

  &:hover .rate__link::after {
    height: 100%;
    transition-delay: 0.9s;
  }

  &:hover .rate__item-title {
    color: var(--red-color);
  }

  &:hover .rate__item-text {
    opacity: .7;
  }

  &::before {
    transition-delay: 0.9s;
  }

  &::after {
    transition-delay: 0.6s;
  }

  .rate__link::before {
    transition-delay: 0.3s;
  }

  .rate__link::after {
    transition-delay: 0s;
  }
}

@media screen and (max-width:900px) {
  .rate {
    &__container {
      grid-template-columns: 1fr;
    }
  }
}

@media screen and (max-width:500px) {
  .rate {
    &__link {
      padding: var(--mb-3) var(--mb-0-5);
    }
  }
}
