.about {
  &__container {
    gap: var(--mb-1);
    margin-bottom: 50px;
  }

  &__image {
    flex: 0 1 40%;
  }

  &__info {
    flex: 0 1 60%;
  }

  &__text {
    margin-bottom: var(--mb-1);
  }

  &__subtitle {
    margin-bottom: var(--mb-1);
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    row-gap: var(--mb-2);
    column-gap: var(--mb-1);
    margin-bottom: var(--mb-2);
    max-width: 600px;

    &-title {
      display: flex;
      align-items: center;
      gap: 5px;
      margin-bottom: var(--mb-0-75);
      font-size: clamp(1.5rem, 1.286rem + 1.07vw, 2.25rem);
      color: var(--dark-blue-color);
      line-height: var(--line-height);
      font-variant-numeric: tabular-nums;
    }

    &-text {
      color: var(--gray-color);
      line-height: var(--line-height);
    }
  }
}

.chart__title {
    margin-bottom: var(--mb-3);
    font-size: clamp(1.625rem, 1.196rem + 2.14vw, 3.125rem);
    text-align: center;
    line-height: var(--line-height);
    span {
      color: var(--red-color);
      text-decoration: underline;
    }
}

.number {
  display: inline-block;
  min-width: 80px;
  text-align: right;
}

.plus {
  display: inline-block;
}

@media screen and (max-width:1024px) {
  .about {
    &__container {
      flex-direction: column-reverse;
      gap: var(--mb-3);
    }

    &__image {
      margin: auto;
      flex: 1 1 100%;
      width: 70%;
    }

    &__info {
      flex: 1 1 100%;
    }
  }
}



@media screen and (max-width:600px) {
  .about {
    &__image {
      width: 100%;
    }
  }
}
