.audience {
  background-color: #0d2e620d;

  &__container {
    gap: var(--mb-3);
  }

  &__chart {
    width: 50%;
    aspect-ratio: 1 / 1;
  }

  &__info {
    width: 50%;
  }

  &__title {
    padding-left: 0;
    border-left: 0;

    span {
      color: var(--red-color);
      text-decoration: underline;
    }
  }

  &__text {
    margin-bottom: var(--mb-1);
  }

  &__subtitle {
    margin-bottom: var(--mb-1);
    font-size: clamp(1.375rem, 1.232rem + 0.71vw, 1.875rem);
    text-align: center;
  }
}

#audienceMap {
  width: 100%;
  height: 400px;
}

#audienceChart {
  width: 100%;
  height: 100%;
}


.chart-legend {
  display: flex;
  flex-direction: column;
  gap: 10px;
  font-family: sans-serif;
  font-size: 14px;
}

@media (max-width: 768px) {
  .chart-wrapper {
    flex-direction: column;
  }

  .chart__title span {
    display: block;
  }

  .audience {
    &__container {
      flex-direction: column;
    }

    &__info,
    &__chart {
      width: 100%;
    }
  }

  #audienceChart {
    width: 100% !important;
    height: 100% !important;
  }
}

canvas { display: block; }
    .label {
      position: absolute;
      color: #fff;
      background: rgba(13, 110, 253, 0.85);
      padding: 5px 10px;
      border-radius: 6px;
      font-size: 13px;
      pointer-events: none;
      transform: translate(-50%, -50%);
      white-space: nowrap;
    }
